import { Component } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';

@Component({
    selector: 'app-section-contact',
    template: `
        <section class="page-section" id="contact">
            <div class="container">
                <div class="text-center">
                    <h2 class="section-heading text-uppercase">Sugerencias</h2>
                    <h3 class="section-subheading text-muted">Envia un comentario, sugerencia u observación.</h3>
                </div>
                <form id="contactForm" [formGroup]="contactForm">
                    <div class="row align-items-stretch mb-5">
                        <div class="col-md-6">
                            <div class="form-group">
                                <input class="form-control" id="name" type="text" placeholder="Nombres *" required="required"
                                    data-validation-required-message="Por favor, ingresa tu nombre" />
                                <p class="help-block text-danger"></p>
                            </div>
                            <div class="form-group">
                                <input class="form-control" id="email" type="email" placeholder="Correo electrónico *"
                                    required="required" data-validation-required-message="Por favor, ingresa tu dirección de email." />
                                <p class="help-block text-danger"></p>
                            </div>
                            <div class="form-group mb-md-0">
                                <input class="form-control" id="phone" type="tel" placeholder="Número celular *" required="required"
                                    data-validation-required-message="Por favor, ingresa tu número celular." />
                                <p class="help-block text-danger"></p>
                            </div>
                        </div>
                        <div class="col-md-6">
                            <div class="form-group form-group-textarea mb-md-0">
                                <textarea class="form-control" id="message" placeholder="Mensaje *" required="required"
                                    data-validation-required-message="Por favor, ingresa tu mensaje"></textarea>
                                <p class="help-block text-danger"></p>
                            </div>
                        </div>
                    </div>
                    <div class="text-center">
                        <div id="success"></div>
                        <button class="btn btn-primary btn-xl text-uppercase" id="sendMessageButton" type="submit">Enviar
                            Mensaje</button>
                    </div>
                </form>
            </div>
        </section>`
})
export class SectionContactComponent {
    contactForm = this.fb.group({
        username: ['', Validators.required],
        password: ['', Validators.required],
    });

    constructor(private fb: FormBuilder) {
    }
}
