import { Component, Output, EventEmitter } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { AuthService } from '../auth.service';

@Component({
    selector: 'app-section-auth',
    template: `
        <section class="page-section bg-light" id="tools">
            <div class="container">
                <div class="text-center">
                    <h2 class="section-heading text-uppercase">Autenticación</h2>
                    <h3 class="section-subheading text-muted">Ingresa tus datos de acceso para utilizar las herramientas.</h3>
                </div>
                <form id="authForm" [formGroup]="authForm">
                    <div class="row align-items-stretch mb-5">
                        <div class="col-md-3"></div>
                        <div class="col-md-6">
                            <div class="form-group">
                                <span class="help-block">Usuario</span>
                                <input class="form-control" id="username" type="text" placeholder="Usuario *" required="required"
                                    data-validation-required-message="Ingresa tu usuario." formControlName="username"/>
                                <p class="help-block text-danger"></p>
                            </div>
                            <div class="form-group mb-md-0">
                                <span class="help-block">Contraseña</span>
                                <input class="form-control" id="password" type="password" placeholder="Contraseña *" required="required"
                                    data-validation-required-message="Ingresa tu contraseña." autocomplete="password" 
                                    formControlName="password"/>
                                <p class="help-block text-danger"></p>
                            </div>
                        </div>
                    </div>
                    <div class="text-center" *ngIf="message_error">
                        <p class="help-block text-danger">{{message_error}}</p>
                    </div>
                    <div class="text-center">
                        <div id="success"></div>
                        <button class="btn btn-primary btn-xl text-uppercase" (click)="validar()">Entrar</button>
                    </div>
                </form>
            </div>
        </section>
        
        <!-- Clients-->
    <div class="py-5 mt-4">
        <div class="container">
            <div class="row">
                <div class="col-md-3 col-sm-6 my-3">
                    <a href="http://10.2.202.208:8080/webhc/" target="_blank">
                    <img class="img-fluid d-block mx-auto" src="assets/img/logos/envato.jpg" alt="" /></a>
                </div>
                <div class="col-md-3 col-sm-6 my-3">
                    <a href="http://10.2.202.210:8080/comercial/" target="_blank">
                    <img class="img-fluid d-block mx-auto" src="assets/img/logos/designmodo.jpg" alt="" /></a>
                </div>
                <div class="col-md-3 col-sm-6 my-3">
                    <a href="http://10.2.202.210:8080/NeoGenesis/" target="_blank">
                    <img class="img-fluid d-block mx-auto" src="assets/img/logos/themeforest.jpg" alt="" /></a>
                </div>
                <div class="col-md-3 col-sm-6 my-3">
                    <a href="#"  target="_blank">
                    <img class="img-fluid d-block mx-auto" src="assets/img/logos/creative-market.jpg" alt="" /></a>
                </div>
            </div>
        </div>
    </div>`
})
export class SectionAuthComponent {
    @Output() onAutenticar: EventEmitter<any> = new EventEmitter<any>();
    message_error: string;
    authForm = this.fb.group({
        username: ['', Validators.required],
        password: ['', Validators.required],
    });

    constructor(private fb: FormBuilder, private authService: AuthService) {

    }

    validar() {
        this.authService.validarCredenciales(this.authForm.value.username, this.authForm.value.password).subscribe(response => {
            this.authForm.controls.username.disable({});
            this.authForm.controls.password.disable({});
            const data: any = {};
            data.name = 'nb:auth:oauth2:token';
            data.ownerStrategyName = 'auth';
            data.createdAt = (new Date()).getTime();
            data.value = JSON.stringify(response.body);
            localStorage.setItem('auth_app_token', JSON.stringify(data));
            localStorage.setItem('token', JSON.stringify(response.body));
            this.onAutenticar.emit(response);
        }, err => {
            console.log(err);
            this.message_error = err;
            // this.appFuncionesService.delay(2000).then(() => {
            //     this.showSpinner(false, 'text-danger', err);
            // });
        });
        console.log(this.authForm.value);
    }
}
